<template>
  <div class="wrap pt1">
    <div class="course-content">
      <div class="course-box">
        <filter-bar
          name="weekFilter"
          :islive="true"
          showBuild
          showFloor
          showClsRoom
          is-rec-plan
          :deviceType="1"
          :pTypeList="pTypeList"
          :lessonList="lessonList"
          :gradeList="gradeList"
          :teacherList="teacherList"
          :schoolList="schoolList"
          :buildingList="buildingList"
          :floorList="floorList"
          :clsroomList="clsroomList"
          @fetchData="filterFetch"
          @fetch="filter"
        ></filter-bar>
        <div class="select-bar">
          <el-radio-group v-model="status" size="small">
            <el-radio-button
              label="历史直播"
              @click.native="fetchLivetype('2')"
            ></el-radio-button>
            <el-radio-button
              label="正在进行"
              @click.native="fetchLivetype('0')"
            ></el-radio-button>
            <el-radio-button
              label="即将开始"
              @click.native="fetchLivetype('1')"
            ></el-radio-button>
          </el-radio-group>
          <el-radio-group v-model="time" size="small" class="time-tab">
            <el-radio-button
              label="今日直播"
              @click.native="fetchDatetype('1')"
            ></el-radio-button>
            <el-radio-button
              label="本周直播"
              @click.native="fetchDatetype('2')"
            ></el-radio-button>
          </el-radio-group>
          <el-time-picker
            class="fr"
            size="small"
            is-range
            v-model="timeRange"
            range-separator="至"
            start-placeholder="开始时间"
            end-placeholder="结束时间"
            placeholder="选择时间范围"
          ></el-time-picker>
        </div>
        <div class="courses-box" id="roomPanel">
          <select-container
            v-loading="loading"
            :course-list="courseList"
            :page-size="pageSize"
            :total="allItems"
            :live="true"
            :islive="true"
            @pageChange="fetchData(val)"
          ></select-container>
          <side-recommend title="推荐直播" :isLive="true"></side-recommend>
        </div>
      </div>
    </div>
    <footer-infor></footer-infor>
  </div>
</template>
<script>
import filterBar from "../components/filterBar";
import http from "../utils/http";
import api from "../utils/api";
// import bigBanner from "../components/bigBanner";
import selectBox from "../components/courseSelect";
import selectContainer from "../components/selectContainer.vue";
import sideRecommend from "../components/sideRecommend";
import { formatDate } from "../utils/utils.js";
import footerInfor from "../components/footerInfor";
import "signalr";
//import { truncate } from "fs";
let conn = {};
let hub;
export default {
  components: {
    // bigBanner,
    selectBox,
    selectContainer,
    sideRecommend,
    footerInfor,
    filterBar
  },
  filters: {
    formatDate(time) {
      if (time) {
        return formatDate(time, "yyyy-MM-dd hh:mm");
      } else {
        return "-";
      }
    }
  },
  data() {
    return {
      key: "",
      loading: true,
      // bannerImg: "'../static/zxzb.png'",
      status: "正在进行",
      time: "今日直播",
      timeRange: [
        new Date(new Date(new Date().toLocaleDateString()).getTime()),
        new Date(
          new Date(new Date().toLocaleDateString()).getTime() +
            24 * 60 * 60 * 1000 -
            1
        )
      ],
      pagesize: 12,
      allItems: 0,
      courseList: [],
      sfield: "",
      sdir: 1,
      pageSize: http.pageSize,
      total: 1000,
      date: "",
      dept: "",
      cids: "",
      bids: "",
      fs: "",
      pt: "",
      less: "",
      zynj: "",
      clsids: "",
      pTypeList: [
        {
          text: "录制",
          code: "pt", // 参数名称
          id: "1" // 参数值
        },
        {
          text: "直播",
          code: "pt", // 参数名称
          id: "2" // 参数值
        }
      ],
      teachers: "",
      lessonList: [],
      gradeList: [],
      teacherList: [],
      schoolList: [],
      buildingList: [],
      floorList: [],
      clsroomList: [],
      count: 0,
      nowpage: 0,
      livetype: "0", //当前直播状态 2历史直播 0正在直播 1即将开始
      datetype: "1",
      loadnowpage: 1
    };
  },
  watch: {
    $route(to, from) {
      this.getRouteDatas();
    }
  },
  mounted() {
    console.log(2336);
    // this.fetchBanner();
    this.getRouteDatas();
    // this.LoadScreen();
    // this.openSocket();
  },
  methods: {
    async openSocket() {
      let signalrURL = await api.GetSignalrURL();
      conn = $.hubConnection(signalrURL);
      hub = conn.createHubProxy("TerminalHub");
      conn.qs = { token: http.getToken() };
      var $this = this;
      hub.on("ReceiveALLRECStatus", function(jsonInfo) {
        var recList = JSON.parse(jsonInfo);
        if ($this.livetype == "0") {
          $this.courseList = [];
          let key = "";
          if ($this.$route.query.selectData) {
            let receivearray = JSON.parse($this.$route.query.selectData);
            receivearray.forEach(element => {
              if (element.title === "搜索") {
                key = element.text;
              }
            });
          }
          recList.forEach(element => {
            if (element.LiveEstate == "1") {
              let add = false;
              if (key != "") {
                if (
                  (element.RecTitle && element.RecTitle.search(key) != -1) ||
                  (element.ZYNJ && element.ZYNJ.search(key) != -1) ||
                  (element.TeacherName &&
                    element.TeacherName.search(key) != -1) ||
                  (element.ClsName && element.ClsName.search(key) != -1) ||
                  (element.ClsRoomName && element.ClsRoomName.search(key) != -1)
                ) {
                  add = true;
                }
              } else {
                add = true;
              }
              if (add) {
                //新增卡片页
                $this.courseList.push({
                  title: element.RecTitle,
                  imgSrc: "../../static/onlive.png",
                  label: element.ZYNJ,
                  teacher: element.TeacherName,
                  id: element.RECID,
                  clsid: element.ClsRoomID,
                  lesson: formatDate(element.RECStartTime, "hh:MM:ss"),
                  class: element.ClsName,
                  date: formatDate(element.RECStartTime, "yyyy-MM-dd"),
                  roomname: element.ClsRoomName,
                  time: formatDate(element.RECStartTime, "hh:MM:ss")
                });
              }
            }
          });
        } else {
          recList.forEach(element => {
            //var existtemp = false;
            $this.courseList.forEach(ele => {
              //正式直播 结束
              if (
                ele.recid == element.RECID &&
                ele.recid != null &&
                element.LiveEstate == "0"
              ) {
                //改变图片
                ele.imgSrc = "../../static/lived.png";
              }
              if (
                ele.recid == element.RECID &&
                ele.recid != null &&
                element.LiveEstate == "1"
              ) {
                //改变图片
                ele.imgSrc = "../../static/onlive.png";
              }
              var eletime = new Date(ele.date + " " + ele.time);
              //临时直播 结束
              if (
                ele.clsid == element.ClsRoomID &&
                ele.recid == null &&
                eletime == element.RECStartTime &&
                element.LiveEstate == "0"
              ) {
                //改变图片
                ele.imgSrc = "../../static/lived.png";
              }
              if (
                ele.clsid == element.ClsRoomID &&
                ele.recid == null &&
                eletime == element.RECStartTime &&
                element.LiveEstate == "1"
              ) {
                //改变图片
                ele.imgSrc = "../../static/onlive.png";
              }
            });
          });
        }
      });
      conn
        .start(() => {
          /*  this.$notify({
            title: "开始",
            message: "正在开启实时状态同步",
            type: "info",
            duration: 2599
          }); */
        })
        .done(data => {
          hub.invoke("JoinGroup", "ALLREC");
          /* setTimeout(() => {
            this.$notify({
              title: "成功",
              message: "状态同步开启成功",
              type: "success",
              duration: 2599
            });
          }, 1520); */
        })
        .fail(err => {
          /* setTimeout(() => {
            conn.start();
            this.$notify.error({
              title: "错误",
              message: "状态同步开启失败"
            });
          }, 1520); */
        });
    },
    filterFetch(val) {},
    filter(val) {
      let cidstemp = "";
      let bidstemp = "";
      let fstemp = "";
      let ptemp = "";
      let lesstemp = "";
      let zynjtemp = "";
      let teachertemp = "";
      let clsidtemp = "";
      val.forEach(element => {
        switch (element.code) {
          case "cids":
            cidstemp += element.id + ",";
            break;
          case "bids":
            bidstemp += element.id + ",";
            break;
          case "fs":
            fstemp += element.id + ",";
            break;
          case "pt":
            ptemp += element.id + ",";
            break;
          case "less":
            lesstemp += "'" + element.id + "',";
            break;
          case "zynj":
            zynjtemp += "'" + element.id + "',";
            break;
          case "teach":
            teachertemp += element.id + ",";
            break;
          case "clsids":
            clsidtemp += element.id + ",";
            break;
          default:
            break;
        }
      });
      this.clsids = clsidtemp.slice(0, clsidtemp.length - 1);
      this.cids = cidstemp.slice(0, cidstemp.length - 1);
      this.bids = bidstemp.slice(0, bidstemp.length - 1);
      this.fs = fstemp.slice(0, fstemp.length - 1);
      this.pt = ptemp.slice(0, ptemp.length - 1);
      this.less = lesstemp.slice(0, lesstemp.length - 1);
      this.zynj = zynjtemp.slice(0, zynjtemp.length - 1);
      this.teachers = teachertemp.slice(0, teachertemp.length - 1);
      this.LoadScreen();
    },
    LoadScreen() {
      this.fetchfilterData([
        api.getAllCampusesData,
        {
          token: http.getToken()
        },
        0
      ]);
      //大楼
      this.fetchfilterData([
        api.getBuildingDatasByCT,
        {
          token: http.getToken(),
          cids: this.cids,
          type: 1
        },
        1
      ]);
      //楼层
      this.fetchfilterData([
        api.getFloorByBuilding,
        {
          token: http.getToken(),
          cids: this.cids,
          bids: this.bids
        },
        2
      ]);
      //课程、专业、教师
      this.fetchfilterData([
        api.GetRecScreenCondition,
        {
          date: "1",
          cids: this.cids,
          bids: this.bids,
          fs: this.fs,
          token: http.getToken()
        },
        3
      ]);
      //教室
      this.fetchfilterData([
        api.getDatasByCampusBuildingFloors,
        {
          cids: this.cids,
          bids: this.bids,
          floors: this.fs,
          token: http.getToken()
        },
        4
      ]);
      //数据
      this.fetchData(0);
    },
    fetchfilterData: async function(paramsArr) {
      let url = paramsArr[0];
      let params = paramsArr[1];
      let type = paramsArr[2];
      let res = await http.get(url, params);
      if (res.Datas) {
        switch (type) {
          case 0:
            //校区
            if (res.Datas.length == 1) {
              this.isSchoolHide = true;
            } else {
              this.schoolList = [];
              res.Datas.forEach(element => {
                this.schoolList.push({
                  text: element.CampusesName,
                  code: "cids",
                  id: element.CampusesID
                });
              });
            }
            break;
          case 1:
            //教学楼
            this.buildingList = [];
            res.Datas.forEach(element => {
              this.buildingList.push({
                text: element.BuildingName,
                code: "bids",
                id: element.BuildingID
              });
            });
            break;
          case 2:
            //楼层
            this.floorList = [];
            res.Datas.forEach(element => {
              this.floorList.push({
                text: "第" + element + "层",
                code: "fs",
                id: element
              });
            });
            break;
          case 3:
            //专业、课程
            this.lessonList = [];
            this.gradeList = [];
            this.teacherList = [];
            if (res.Datas.Title) {
              res.Datas.Title.forEach(element => {
                this.lessonList.push({
                  text: element,
                  code: "less",
                  id: element
                });
              });
            }
            if (res.Datas.ZYNJ) {
              res.Datas.ZYNJ.forEach(element => {
                this.gradeList.push({
                  text: element,
                  code: "zynj",
                  id: element
                });
              });
            }
            if (res.Datas.Teachers) {
              res.Datas.Teachers.forEach(element => {
                this.teacherList.push({
                  text: element,
                  code: "teach",
                  id: element
                });
              });
            }
            break;
          case 4:
            //教室
            this.clsroomList = [];
            res.Datas.forEach(element => {
              this.clsroomList.push({
                text: element.ClsRoomName,
                code: "clsids",
                id: element.ClsRoomID
              });
            });
            break;
          default:
            break;
        }
      }
    },
    getRouteDatas() {
      if (this.$route.params.sfield) {
        this.sfield = this.$route.query.sfield;
      }
      if (this.$route.query.sdir) {
        this.sdir = this.$route.query.sdir;
      }
      if (this.$route.query.selectData) {
        let receivearray = JSON.parse(this.$route.query.selectData);
        receivearray.forEach(element => {
          if (element.title === "搜索") {
            this.key = element.text;
          }
        });
      }
      if (this.livetype != "0") this.fetchData(0);
      else {
      }
    },
    //获取历史直播 当前直播 即将开始
    fetchLivetype(type) {
      this.livetype = type;
      this.fetchDataByTopCon();
    },
    //获取今日 还是本周
    fetchDatetype(type) {
      this.datetype = type;
      this.fetchDataByTopCon();
    },
    fetchDataByTopCon() {
      //非当前直播
      if (this.livetype != "0") {
        this.fetchData();
      }
    },
    fetchData(val = "") {
      let params = {
        key: this.key,
        date: "",
        cids: this.cids,
        bids: this.bids,
        floors: this.fs,
        less: this.less,
        gras: this.zynj,
        dept: "",
        clsroom: this.clsids,
        starttime: formatDate(this.timeRange[0], "yyyy-MM-dd hh:mm:ss"),
        endtime: formatDate(this.timeRange[1], "yyyy-MM-dd hh:mm:ss"),
        livetype: this.livetype,
        datetype: this.datetype,
        sfield: "rec.RECStartTime",
        sdir: this.sdir,
        teachers: this.teachers,
        psize: this.pagesize,
        pno: val
      };
      this.nowpage = val;
      this.fetchLive(params);
    },
    fetchMore() {
      this.loadnowpage++;
      let params = {
        key: this.key,
        date: "",
        cids: this.cids,
        bids: this.bids,
        floors: this.fs,
        less: this.less,
        gras: this.zynj,
        dept: "",
        clsroom: this.clsids,
        starttime: formatDate(this.timeRange[0], "yyyy-MM-dd hh:mm:ss"),
        endtime: formatDate(this.timeRange[1], "yyyy-MM-dd hh:mm:ss"),
        livetype: this.livetype,
        datetype: this.datetype,
        sfield: "rec.RECStartTime",
        sdir: this.sdir,
        teachers: this.teachers,
        psize: this.pagesize,
        pno: this.loadnowpage
      };
      this.fetchLiveMore(params);
    },
    /*   async fetchCourseBySch() {
      const res = await http.get(api.getCourseBySch, {});
      if (res.Result) {
        if (res.SchoolType === 1) {
          this.$refs.selectBox.gradeordisciplineName = "专业";
        } else {
          this.$refs.selectBox.gradeordisciplineName = "年级";
        }
        this.$refs.selectBox.majorcourseList = [];
        this.$refs.selectBox.majorList = [];
        res.Datas.forEach(element => {
          let temp = [];
          element.Value.forEach(ele => {
            temp.push({
              check: false,
              id: `'${ele.CourseName}'`,
              text: ele.CourseName
            });
          });
          this.$refs.selectBox.majorList.push({
            check: false,
            id: `'${element.ClassifyName}'`,
            text: element.ClassifyName,
            course: temp
          });
        });
        this.$refs.selectBox.majorcourseList = this.$refs.selectBox.majorList[0].course;
      }
    }, */
    // async fetchBanner() {
    //   let params = {
    //     location: "3",
    //     key: "",
    //     sfield: "",
    //     sdir: "",
    //     estate: "all",
    //     pno: 1
    //   };
    //   const res = await http.get(api.getSilderPic, params);
    //   if (res.Result) {
    //     res.Datas.forEach(element => {
    //       this.bannerImg = `${api.DownloadFile}?filename=${
    //         element.SCID
    //       }&fileType=7`;
    //     });
    //   }
    // },
    async fetchLive(params) {
      const res = await http.get(api.getLiveByCondition, params);
      this.loading = false;
      if (res.Result) {
        this.courseList = [];
        let imgsrc = "";
        if (this.livetype == "2") {
          imgsrc = "../../static/lived.png";
        }
        if (this.livetype == "1") {
          imgsrc = "../../static/unlive.png";
        }
        if (this.livetype == "0") {
          imgsrc = "../../static/onlive.png";
        }
        res.Datas.forEach(element => {
          this.courseList.push({
            title: element.Key.CSTitle,
            imgSrc: imgsrc,
            label: element.Name,
            teacher: element.TeacherName,
            id: element.Key.RECID,
            clsid: element.Key.ClsRoomID,
            lesson: formatDate(element.Key.RECStartTime, "hh:MM:ss"),
            class: element.ClsName,
            date: formatDate(element.Key.RECStartTime, "yyyy-MM-dd"),
            roomname: element.RoomName,
            time: formatDate(element.Key.RECStartTime, "hh:MM:ss")
          });
        });
      }
      this.allItems = res.RowsCount;
      this.pageSize = res.PageSize;
    },
    async fetchLiveMore(params) {
      const res = await http.get(api.getLiveByCondition, params);
      this.loading = false;
      if (res.Result) {
        let imgsrc = "";
        if (this.livetype == "2") {
          imgsrc = "../../static/lived.png";
        }
        if (this.livetype == "1") {
          imgsrc = "../../static/unlive.png";
        }
        if (this.livetype == "0") {
          imgsrc = "../../static/onlive.png";
        }
        res.Datas.forEach(element => {
          this.courseList.push({
            title: element.Key.CSTitle,
            imgSrc: imgsrc,
            label: element.Name,
            teacher: element.TeacherName,
            id: element.Key.RECID,
            clsid: element.Key.ClsRoomID,
            lesson: formatDate(element.Key.RECStartTime, "hh:MM:ss"),
            class: element.ClsName,
            date: formatDate(element.Key.RECStartTime, "yyyy-MM-dd"),
            roomname: element.RoomName,
            time: formatDate(element.Key.RECStartTime, "hh:MM:ss")
          });
        });
      }
      this.allItems = res.RowsCount;
      this.pageSize = res.PageSize;
    }
  }
};
</script>
<style lang="less" scoped>
.course-box {
  .select-bar {
    width: 100%;
    background: #fff;
    padding: 0 20px;
    padding-top: 8px;
    text-align: left;
    .time-tab {
      margin-left: 8px;
    }
  }
}
</style>



