<template>
  <div class="all-rooms-header">
    <div class="select-header">
      <div class="all-rooms-text">
        所有教室&nbsp;
        <span v-for="(item,index) in pathList" :key="index">
          >&nbsp;
          <span class="path-item">
            {{item.title}}:&nbsp;
            <span>{{item.text}}</span>
            <i class="iconfont icon-delete_s" @click="deletehPath(index)"></i>
          </span>
          &nbsp;
        </span>
      </div>
      <div class="select-btn" @click="toggleChoose">
        {{toggleText}}筛选&nbsp;
        <i
          class="glyphicon iconfont"
          :class="{'icon-angle-up':chooseShow,'icon-angle-down':!chooseShow}"
        ></i>
      </div>
      <div class="select-btn" @click="cleanChoose">
        清空筛选&nbsp;
        <i
          class="glyphicon iconfont"
          :class="{'icon-angle-up':chooseShow,'icon-angle-down':!chooseShow}"
        ></i>
      </div>
      <div style="float:right;margin-right:20px" v-if="!islive">
        <el-date-picker
          v-model="dataRange"
          type="daterange"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          range-separator="到"
          @change="datepickerchange"
        ></el-date-picker>
      </div>
      <slot name="header-btns"></slot>
    </div>
    <transition name="fade">
      <div class="select-box" id="selectBox" v-if="chooseShow">
        <!-- 状态 -->
        <!-- <div v-show="statusList!=null" class="select-line" :style="'height:' + eqHeight + 'px'">
          <div class="select-item">
            <div class="condition-title">{{statusLabel}}：</div>
            <div class="item-box">
              <div
                class="item"
                v-for="(item,index) in statusList"
                :key="index"
                @click="statusClick(index)"
              >{{item.text}}</div>
            </div>
          </div>
          <div
            class="more-choose"
            v-if="eqTooMuch"
            @click="isEqMore = !isEqMore"
          >{{isEqMore ? '收起' : '更多'}}</div>
        </div>-->
        <!-- 校区 -->
        <div v-show="!isSchoolHide" class="select-line" :style="'height:' + schoolHeight + 'px'">
          <div class="select-item">
            <div class="condition-title">校区：</div>
            <div class="item-box">
              <div
                class="item"
                v-for="(item,index) in schoolList"
                :key="index"
                @click="schoolClick(index)"
              >{{item.text}}</div>
            </div>
          </div>
          <div
            class="more-choose"
            v-if="schoolTooMuch"
            @click="isSchoolMore = !isSchoolMore"
          >{{isSchoolMore ? '收起' : '更多'}}</div>
        </div>
        <!-- 建筑物 -->
        <div v-show="showBuild" class="select-line" :style="'height:' + buildingHeight + 'px'">
          <div class="select-item">
            <div class="condition-title">建筑物(楼)：</div>
            <div class="item-box">
              <div
                class="item"
                v-for="(item,index) in buildingList"
                :key="index"
                @click="buildingClick(index)"
              >{{item.text}}</div>
            </div>
          </div>
          <div
            class="more-choose"
            v-if="buildingTooMuch"
            @click="isBuildingMore = !isBuildingMore"
          >{{isBuildingMore ? '收起' : '更多'}}</div>
        </div>
        <!-- 楼层 -->
        <div v-show="showFloor" class="select-line" :style="'height:' + floorHeight + 'px'">
          <div class="select-item">
            <div class="condition-title">楼层：</div>
            <div class="item-box">
              <div
                class="item"
                v-for="(item,index) in floorList"
                :key="index"
                @click="floorClick(index)"
              >{{item.text}}</div>
            </div>
          </div>
          <div
            class="more-choose"
            v-if="floorTooMuch"
            @click="isFloorMore = !isFloorMore"
          >{{isFloorMore ? '收起' : '更多'}}</div>
        </div>
        <!-- 教室-->
        <div v-show="showClsRoom" class="select-line" :style="'height:' + clsroomHeight + 'px'">
          <div class="select-item">
            <div class="condition-title">教室：</div>
            <div class="item-box">
              <div
                class="item"
                v-for="(item,index) in clsroomList"
                :key="index"
                @click="clsroomClick(index)"
              >{{item.text}}</div>
            </div>
          </div>
          <div
            class="more-choose"
            v-if="ClsRoomTooMuch"
            @click="isClsRoomMore = !isClsRoomMore"
          >{{isClsRoomMore ? '收起' : '更多'}}</div>
        </div>
        <!-- 计划类型，限录播计划使用 -->
        <!--   <div v-if="isRecPlan" class="select-line" :style="'height:' + pTypeHeight + 'px'">
          <div class="select-item">
            <div class="condition-title">计划类型：</div>
            <div class="item-box">
              <div
                class="item"
                v-for="(item,index) in pTypeList"
                :key="index"
                @click="pTypeClick(index)"
              >{{item.text}}</div>
            </div>
          </div>
          <div
            class="more-choose"
            v-if="pTypeTooMuch"
            @click="isPTypeMore = !isPTypeMore"
          >{{isPTypeMore ? '收起' : '更多'}}</div>
        </div>-->
        <!-- 课程，限录播计划使用 -->
        <div v-if="isRecPlan" class="select-line" :style="'height:' + lessonHeight + 'px'">
          <div class="select-item">
            <div class="condition-title">课程：</div>
            <div class="item-box">
              <div
                class="item"
                v-for="(item,index) in lessonList"
                :key="index"
                @click="lessonClick(index)"
              >{{item.text}}</div>
            </div>
          </div>
          <div
            class="more-choose"
            v-if="lessonTooMuch"
            @click="isLessonMore = !isLessonMore"
          >{{isLessonMore ? '收起' : '更多'}}</div>
        </div>
        <!-- 专业/年级，限录播计划使用 -->
        <div v-if="isRecPlan" class="select-line" :style="'height:' + gradeHeight + 'px'">
          <div class="select-item">
            <div class="condition-title">专业/年级：</div>
            <div class="item-box">
              <div
                class="item"
                v-for="(item,index) in gradeList"
                :key="index"
                @click="gradeClick(index)"
              >{{item.text}}</div>
            </div>
          </div>
          <div
            class="more-choose"
            v-if="gradeTooMuch"
            @click="isGradeMore = !isGradeMore"
          >{{isGradeMore ? '收起' : '更多'}}</div>
        </div>
        <!-- 讲师，限录播计划使用 -->
        <div v-if="isRecPlan" class="select-line" :style="'height:' + teacherHeight + 'px'">
          <div class="select-item">
            <div class="condition-title">讲师：</div>
            <div class="item-box">
              <div
                class="item"
                v-for="(item,index) in teacherList"
                :key="index"
                @click="teacherClick(index)"
              >{{item.text}}</div>
            </div>
          </div>
          <div
            class="more-choose"
            v-if="teacherTooMuch"
            @click="isTeacherMore = !isTeacherMore"
          >{{isTeacherMore ? '收起' : '更多'}}</div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import http from "../utils/http";
export default {
  props: {
    islive: {
      type: Boolean,
      default: false
    },
    getBuildingURL: String,
    getFloorURL: String,
    getItemURL: String,
    offsetHeight: Number,
    // statusList: Array,
    schoolList: Array,
    buildingList: Array,
    floorList: Array,
    clsroomList: Array,
    isSchoolHide: Boolean,
    showBuild: Boolean,
    showFloor: Boolean,
    showClsRoom: Boolean,
    deviceType: Number,
    name: String,
    // 录播计划
    isRecPlan: {
      type: Boolean,
      default: false
    },
    // pTypeList: Array,
    lessonList: Array,
    gradeList: Array,
    teacherList: Array
  },
  data() {
    return {
      dataRange: [
        new Date(Date.now() - 8.64 * Math.pow(10, 7) * 30),
        new Date()
      ],
      boxHeight: 0,
      statusLabel: "设备状态",
      toggleText: "收起",
      pathList: [],
      chooseShow: true,

      // eqTooMuch: false,
      // isEqMore: false,
      // eqHeight: 50,

      schoolTooMuch: false,
      isSchoolMore: false,
      schoolHeight: 50,

      buildingTooMuch: false,
      isBuildingMore: false,
      buildingHeight: 50,

      floorTooMuch: false,
      isFloorMore: false,
      floorHeight: 50,

      ClsRoomTooMuch: false,
      isClsRoomMore: false,
      clsroomHeight: 50,

      showStateLine: true,
      checkList: [],
      sessionName: "FJRH_9528_",

      // 录播计划
      // pTypeTooMuch: false,
      // isPTypeMore: false,
      // pTypeHeight: 50,

      lessonTooMuch: false,
      isLessonMore: false,
      lessonHeight: 50,

      gradeTooMuch: false,
      isGradeMore: false,
      gradeHeight: 50,

      teacherTooMuch: false,
      isTeacherMore: false,
      teacherHeight: 50
    };
  },
  watch: {
    // statusList: {
    //   handler(newVal, oldVal) {
    //     setTimeout(() => {
    //       if ($(".select-line")[0].scrollHeight > 50) {
    //         this.eqTooMuch = true;
    //       }
    //     }, 125);
    //   }
    // },
    schoolList: {
      handler(newValue, oldValue) {
        setTimeout(() => {
          if ($(".select-line")[0].scrollHeight > 50) {
            this.schoolTooMuch = true;
          }
        }, 125);
      },
      deep: true
    },
    buildingList: {
      handler(newValue, oldValue) {
        setTimeout(() => {
          if ($(".select-line")[1].scrollHeight > 50) {
            this.buildingTooMuch = true;
          }
        }, 125);
      },
      deep: true
    },
    floorList: {
      handler(newValue, oldValue) {
        setTimeout(() => {
          if ($(".select-line")[2].scrollHeight > 50) {
            this.floorTooMuch = true;
          }
        }, 125);
      },
      deep: true
    },
    clsroomList: {
      handler(newValue, oldValue) {
        setTimeout(() => {
          if ($(".select-line")[3].scrollHeight > 50) {
            this.ClsRoomTooMuch = true;
          }
        }, 125);
      },
      deep: true
    },
    // pTypeList: {
    //   handler(newValue, oldValue) {
    //     setTimeout(() => {
    //       if ($(".select-line")[3].scrollHeight > 50) {
    //         this.pTypeTooMuch = true;
    //       }
    //     }, 125);
    //   },
    //   deep: true
    // },
    lessonList: {
      handler(newValue, oldValue) {
        setTimeout(() => {
          try {
            if ($(".select-line")[4].scrollHeight > 50) {
              this.lessonTooMuch = true;
            }
          } catch (e) {}
        }, 125);
      },
      deep: true
    },
    gradeList: {
      handler(newValue, oldValue) {
        setTimeout(() => {
          try {
            if ($(".select-line")[5].scrollHeight > 50) {
              this.gradeTooMuch = true;
            }
          } catch (e) {}
        }, 125);
      },
      deep: true
    },
    teacherList: {
      handler(newValue, oldValue) {
        setTimeout(() => {
          try {
            if ($(".select-line")[6].scrollHeight > 50) {
              this.teacherTooMuch = true;
            }
          } catch (e) {}
        }, 125);
      },
      deep: true
    },
    // isEqMore(newVal, oldVal) {
    //   let scrollHeight = $(".select-line")[0].scrollHeight;
    //   this.eqHeight = newVal ? scrollHeight : 50;
    //   this.initPanelHeight(false, newVal ? scrollHeight - 50 : 0);
    // },
    isSchoolMore(newVal, oldVal) {
      let scrollHeight = $(".select-line")[0].scrollHeight;
      this.schoolHeight = newVal ? scrollHeight : 50;
      this.overflow(this.isSchoolMore, ".select-line:nth-child(1) .item-box");
      this.initPanelHeight(false, newVal ? scrollHeight - 50 : 0);
    },
    isBuildingMore(newVal, oldVal) {
      let scrollHeight = $(".select-line")[1].scrollHeight;
      this.buildingHeight = newVal ? scrollHeight : 50;
      this.overflow(this.isBuildingMore, ".select-line:nth-child(2) .item-box");
      this.initPanelHeight(false, newVal ? scrollHeight - 50 : 0);
    },
    isFloorMore(newVal, oldVal) {
      let scrollHeight = $(".select-line")[2].scrollHeight;
      this.floorHeight = newVal ? scrollHeight : 50;
      this.overflow(this.isFloorMore, ".select-line:nth-child(3) .item-box");
      this.initPanelHeight(false, newVal ? scrollHeight - 50 : 0);
    },
    isClsRoomMore(newVal, oldVal) {
      let scrollHeight = $(".select-line")[3].scrollHeight;
      this.clsroomHeight = newVal ? scrollHeight : 50;
      this.overflow(this.isClsRoomMore, ".select-line:nth-child(4) .item-box");
      this.initPanelHeight(false, newVal ? scrollHeight - 50 : 0);
    },
    // isPTypeMore(newVal, oldVal) {
    //   let scrollHeight = $(".select-line")[4].scrollHeight;
    //   this.pTypeHeight = newVal ? scrollHeight : 50;
    //   this.initPanelHeight(false, newVal ? scrollHeight - 50 : 0);
    // },
    isLessonMore(newVal, oldVal) {
      let scrollHeight = $(".select-line")[4].scrollHeight;
      this.lessonHeight = newVal ? scrollHeight : 50;
      this.overflow(this.isLessonMore, ".select-line:nth-child(5) .item-box");
      this.initPanelHeight(false, newVal ? scrollHeight - 50 : 0);
    },
    isGradeMore(newVal, oldVal) {
      let scrollHeight = $(".select-line")[5].scrollHeight;
      this.gradeHeight = newVal ? scrollHeight : 50;
      this.overflow(this.isGradeMore, ".select-line:nth-child(6) .item-box");
      this.initPanelHeight(false, newVal ? scrollHeight - 50 : 0);
    },
    isTeacherMore(newVal, oldVal) {
      let scrollHeight = $(".select-line")[6].scrollHeight;
      this.teacherHeight = newVal ? scrollHeight : 50;
      this.overflow(this.isTeacherMore, ".select-line:last-child .item-box");
      this.initPanelHeight(false, newVal ? scrollHeight - 50 : 0);
    }
  },
  mounted() {
    if (this.name == "ts") {
      this.statusLabel = "教室状态";
    }
    this.sessionName += this.name;
    this.boxHeight = document.getElementById("selectBox").clientHeight + 18;
    this.initPanelHeight();
    window.onresize = () => {
      if (document.getElementById("selectBox")) {
        this.initPanelHeight();
      }
    };
    if (this.$route.params.selectData && !this.islive) {
      let receivearray = JSON.parse(this.$route.params.selectData);
      receivearray.forEach(element => {
        this.pathList.push({
          title: element.title,
          text: element.text,
          code: element.code,
          id: element.id
        });
      });
      this.$emit("fetch", this.pathList);
    }
    // if (window.sessionStorage.getItem(this.sessionName)) {
    //   this.pathList = JSON.parse(
    //     window.sessionStorage.getItem(this.sessionName)
    //   ).a;
    //   this.getTerminalByCondition();
    // }
  },
  beforeDestroy() {
    this.setFilterPath();
  },
  methods: {
    datepickerchange() {
      this.$emit("fetch", this.pathList);
    },
    //是否显示滚动条 selector 选择器
    overflow(f, selector) {
      let flow = null;
      f == true ? (flow = "auto") : (flow = "hidden");
      $(selector).css("overflow", flow);
    },
    setFilterPath() {
      window.sessionStorage.setItem(
        this.sessionName,
        JSON.stringify({ a: this.pathList })
      );
    },
    initPanelHeight(isFlood = false, params = 0) {
      let wrapInnerHeight =
        document.getElementsByClassName("wrap").item(0).clientHeight - 78;
      let statusHeight = 0;
      if (document.getElementById("status")) {
        statusHeight = document.getElementById("status").clientHeight + 18;
      }
      let minues =
        wrapInnerHeight -
        this.offsetHeight -
        this.boxHeight -
        params -
        statusHeight -
        55;
      if (isFlood) {
        minues = wrapInnerHeight - this.offsetHeight - statusHeight - 55 - 12.5;
      }
      document
        .getElementById("roomPanel")
        .setAttribute("style", "height:" + minues + "px");
    },
    toggleChoose() {
      this.chooseShow = !this.chooseShow;
      if (this.chooseShow) {
        this.toggleText = "收起";
        this.initPanelHeight(false);
      } else {
        this.toggleText = "展开";
        setTimeout(() => {
          this.initPanelHeight(true);
        }, 259.9);
      }
    },
    //清空筛选
    cleanChoose() {
      this.pathList = [];
    },
    //删除筛选
    deletehPath(index) {
      this.pathList.splice(index, 1);
      if (this.pathList.length > 0) {
        this.getTerminalByCondition();
      } else {
        this.setFilterPath();
        this.$emit("initDate", "");
      }
      this.$emit("fetch", this.pathList);
    },
    //筛选设备状态
    // statusClick(key) {
    //   this.checkArrHandler(this.statusList, key, "状态");
    //   this.$emit("getByStatus", key);
    // },
    //筛选校区
    schoolClick(key) {
      this.checkArrHandler(this.schoolList, key, "校区");
      //获取大楼
      this.$emit("fetchData", [
        this.getBuildingURL,
        {
          token: http.getToken(),
          cids: this.schoolList[key].id,
          type: this.deviceType
        }
      ]);
      //获取楼层
      this.$emit("fetchData", [
        this.getFloorURL,
        {
          token: http.getToken(),
          cids: this.schoolList[key].id,
          bids: ""
        }
      ]);
      this.$emit("fetch", this.pathList);
    },
    //筛选大楼
    buildingClick(key) {
      this.checkArrHandler(this.buildingList, key, "建筑物");
      //获取楼层
      let paramsCids = "";
      this.pathList.forEach(element => {
        if (element.title == "校区") {
          paramsCids = element.id;
        }
      });
      this.$emit("fetchData", [
        this.getFloorURL,
        {
          token: http.getToken(),
          cids: paramsCids,
          bids: this.buildingList[key].id
        }
      ]);
      this.$emit("fetch", this.pathList);
    },
    //筛选楼层
    floorClick(key) {
      this.checkArrHandler(this.floorList, key, "楼层");
      this.$emit("fetch", this.pathList);
    },
    //筛选教室
    clsroomClick(key) {
      this.checkArrHandler(this.clsroomList, key, "教室");
      this.$emit("fetch", this.pathList);
    },
    //获取筛选后的设备
    getTerminalByCondition() {
      console.log("路径列表", this.pathList);
      if (this.pathList.length > 0) {
        this.loading = true;
        let paramsCids = "";
        let paramsBids = "";
        let paramsFs = "";
        let omgStatus = -1;
        this.pathList.forEach(ele => {
          console.dir("数据", ele);

          switch (ele.title) {
            case "校区":
              paramsCids += ele.id + ",";
              break;
            case "建筑物":
              paramsBids += ele.id + ",";
              break;
            case "楼层":
              paramsFs += ele.id + ",";
              break;
            case "状态":
              omgStatus = ele.code;
          }
        });
        paramsCids = paramsCids.slice(0, paramsCids.length - 1);
        paramsBids = paramsBids.slice(0, paramsBids.length - 1);
        paramsFs = paramsFs.slice(0, paramsFs.length - 1);
        this.$emit("fetchData", [
          this.getItemURL,
          {
            token: http.getToken(),
            cids: paramsCids,
            bids: paramsBids,
            fs: paramsFs,
            status: omgStatus
          }
        ]);
      }
    },
    checkArrHandler(arr, key, text) {
      if (this.pathList.length == 0) {
        this.pathList.push({
          title: text,
          text: arr[key].text,
          code: arr[key].code,
          id: arr[key].id
        });
      } else {
        if (text != "状态") {
          let tempCount = 1;
          let tempCount1 = 0;
          this.pathList.forEach(element => {
            if (element.text != arr[key].text) {
              tempCount1++;
            } else if (element.text == arr[key].text) {
              tempCount = 0;
            }
          });
          if (tempCount && tempCount1) {
            this.pathList.push({
              title: text,
              text: arr[key].text,
              code: arr[key].code,
              id: arr[key].id
            });
          }
        } else {
          let tempCount2 = 0;
          this.pathList.forEach(element => {
            if (element.title == text) {
              element.text = arr[key].text;
              element.id = arr[key].id;
              tempCount2++;
            }
          });
          if (!tempCount2) {
            this.pathList.push({
              title: text,
              text: arr[key].text,
              code: arr[key].code,
              id: arr[key].id
            });
          }
        }
      }
      switch (text) {
        case "状态":
          break;
        case "计划类型":
        case "课程":
        case "专业/年级":
        case "讲师":
          this.$emit("chooseRecPlan", {
            code: arr[key].code,
            id: arr[key].id
          });
          break;
        default:
          this.getTerminalByCondition();
          break;
      }
      this.$emit("countNum", "");
      this.setFilterPath();
    },
    // 录播计划
    // pTypeClick(key) {
    //   this.checkArrHandler(this.pTypeList, key, "计划类型");
    //   this.$emit("fetch", this.pathList);
    // },
    lessonClick(key) {
      this.checkArrHandler(this.lessonList, key, "课程");
      this.$emit("fetch", this.pathList);
    },
    gradeClick(key) {
      this.checkArrHandler(this.gradeList, key, "专业/年级");
      this.$emit("fetch", this.pathList);
    },
    teacherClick(key) {
      this.checkArrHandler(this.teacherList, key, "讲师");
      this.$emit("fetch", this.pathList);
    }
  }
};
</script>
<style lang="less">
.all-rooms-header {
  width: 100%;
  margin-top: 18px;
  margin-bottom: 12.5px;
  .select-header {
    width: 100%;
    height: 30px;
    .all-rooms-text {
      height: 100%;
      float: left;
      font-weight: 550;
      line-height: 30px;
      .path-item {
        padding: 2px;
        border: 1px dotted #000;
        font-weight: 400 !important;
        &:hover {
          border-style: solid;
          .iconfont:hover {
            cursor: pointer;
          }
        }
      }
    }
    .select-btn {
      width: 100px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      float: right;
      background-color: rgb(78, 169, 255);
      color: #fff;
      font-size: 0.8rem;
      user-select: none;
      &:hover {
        cursor: pointer;
      }
    }
    .header-btns {
      float: right;
      margin-right: 8px;
    }
  }
  .select-box {
    width: 100%;
    margin-top: 8px;
    padding: 0 25px;
    background-color: #fff;
    font-size: 15.2px;
    .select-line {
      width: 100%;
      height: 50px;
      max-height: 200px;
      background-color: #fff;
      overflow: hidden;
      border-bottom: 1px dashed #bbb;
      transition: all 0.4s;
      &:last-of-type {
        border-bottom: none;
      }
      .select-item {
        width: 1075px;
        line-height: 50px;
        float: left;
        .condition-title {
          width: 120px;
          float: left;
          color: rgb(161, 161, 161);
        }
        .item-box {
          width: 955px;
          height: auto;
          max-height: 200px;
          display: flex;
          flex-wrap: wrap;
          float: left;
          .item {
            //width: 120px;
            margin: 0px 15px;
            float: left;
            &:hover {
              cursor: pointer;
              color: #ff0036;
            }
          }
        }
      }
      .more-choose {
        width: 50px;
        height: 25px;
        line-height: 22px;
        text-align: center;
        float: left;
        color: rgb(161, 161, 161);
        border: 1px solid #aaa;
        border-radius: 6px;
        margin-left: 25px;
        margin-top: 12.5px;
        font-size: 0.8rem;
        cursor: pointer;
      }
    }
  }
}
/*自定义过渡效果*/
.fade-enter-active,
.fade-leave-active {
  transition: all 0.2599s linear;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.el-range-editor.el-input__inner {
  width: 390px;
  height: 32px;
  padding-left: 15px;
  padding-right: 15px;
}
</style>



